.top-bar {
  padding-block: 24px;
  
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-block: 36px;
  }

  &__icons {
    display: flex;
    gap: 24px;

    @include onTablet {
      gap: 32px;
    }
  }
  
  &__logo {
    display: flex;
  }
}
